import type { CSSProperties } from "react";
import Link from "next/link";
import styles from "./ProductsPage.module.css";
import { productsByCategory } from "../lib/products";

const toId = (value: string) =>
  value
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "");

export default function ProductsPage() {
  return (
    <div className={styles.page}>
      {productsByCategory.map((category) => (
        <section key={category.category} id={toId(category.category)} className={styles.section}>
          <div className="container">
            <div className={styles.sectionHeader} data-reveal>
              <h2>{category.category}</h2>
            </div>
            <div className={styles.grid} data-reveal-group>
              {category.items.map((item) => (
                <article
                  key={item.slug}
                  className={`${styles.card} reveal-item`}
                  style={
                    {
                      "--media-primary": item.images.primary,
                      "--media-hover": item.images.hover
                    } as CSSProperties
                  }
                >
                  <div className={styles.cardMedia} aria-hidden="true">
                    <div className={styles.mediaPrimary}></div>
                    <div className={styles.mediaHover}></div>
                  </div>
                  <h3>{item.title}</h3>
                  <Link className={styles.viewLink} href={`/products/${item.slug}`}>
                    View product
                  </Link>
                </article>
              ))}
            </div>
          </div>
        </section>
      ))}
    </div>
  );
}
